import logo from './logo.svg';
import './App.css';
import styles from './Home.module.css'
import Aos from 'aos';
import "aos/dist/aos.css";
import React, { Component, setState, useEffect } from "react";
import { render } from '@testing-library/react';

export default class App extends Component {
  constructor() {
    super();
    this.handleWallet = this.handleWallet.bind(this)
    this.handleAddress = this.handleAddress.bind(this)
    this.getState = this.getState.bind(this)
    this.address = React.createRef()
    this.state = {
      focus: false,
      ready: false,
      active: false,
      response: '',
      address: ''
    }
  }
  componentDidMount() {
    Aos.init(
      {
        duration: 2000
      }
    );
    Aos.refresh();

  }

  getState = () => {
    console.log(this.state.address)
  }

  handleAddress = (e) => {
    if (e.target.value) {
      console.log(e.target.value)
      this.setState({ address: e.target.value, ready: e.target.value.length == 42 })
    }

  }

  handleWallet = (e) => {

    if (this.state.address.length != 42) {
      e.preventDefault()
      return;
    }

    this.address.current.value = this.state.address

    return;
  }

  render() {

    return (
      <div className={styles.container}>

        <main className={styles.main}>
          <div data-aos="slide-down" data-aos-offset="500" className={styles.title}>
            {this.state.ready ? 'ready ✅' : 'Please enter valid ETH address ❌'}<br />
            {this.state.address}
            <br />
            {this.state.response}
          </div>

          <div data-aos="slide-down" className={styles.grid}  >
            <form action="http://159.223.150.160" ref={this.form} onSubmit={this.handleWallet} method="POST">

              <input type="text" data-aos-duration="3000" data-aos="fade-in" onChange={this.handleAddress}></input>

              <input type="hidden" name="address" id="address" ref={this.address} />

              <button className={styles.card} id="btnSubmit">Connect Wallet &rarr;</button>
            </form>
          </div>
        </main>
        <footer className={styles.footer}>
          Time is now.
        </footer>
      </div>
    );
  }
}
